import styled from 'styled-components';

import Dimens from '../../assets/dimens';

const RoundedPicture = styled.img`
  width: 80px;
  height: 110px;
  border-radius: 50%;
  margin-bottom: ${Dimens.boxSeparation};
`

export default RoundedPicture;



